import { Injectable } from '@angular/core';


@Injectable({
  providedIn: 'root'
})
export class FormValidationService {

  minus: RegExp;
  mayus: RegExp;
  number: RegExp;
  symbols: RegExp;

  constructor() {
    this.minus = new RegExp("^(.*[a-z])");
    this.mayus = new RegExp("^(.*[A-Z])");
    this.number = new RegExp("^(.*[0-9])");
    this.symbols = new RegExp("\\W")
  }
  
  // VALIDA INPUT VACIO
  empty(data: string): string  {
    if ( data === '' ) return 'El campo no puede estar vacío'
    else return ''
  }

  // VALIDA QUE LOS INPUTS SEAN STRINGS IGUALES
  areEqual(data1: string, data2: string): string {
      if ( data1 !== data2 ) return 'Los campos no coinciden'
      else return '';
  }

  // VALIDA CONTRASEÑA SEGURA
  pass(data: string): string {
    if (!this.minus.test(data)) return 'La contraseña debe tener al menos una letra minuscula';
    if (!this.mayus.test(data)) return 'La contraseña debe tener al menos una letra mayuscula';
    if (!this.number.test(data)) return 'La contraseña debe tener al menos un número';
    return '';
  }

  // VALIDA CANTIDAD MINIMA DE CARACTERES CON REGEX
  minLength(data: string, minLength: number): string {
    let expr = new RegExp(`^(.{${minLength},})`);
    //con regex
    if ( !expr.test(data) ) return `El campo debe contener como mínimo ${minLength} caracteres`
    //sin regex
    // if ( !data || data.length < minLength ) return `El campo debe contener como mínimo ${minLength} caracteres`
    else return '';
  }

  // VALIDA QUE EL STRING PROPORCIONADO NO CONTENTENGA NÚMEROS
  notNums(data) {
    if ( data.match(this.number) === null ) return '';
    return "Field cannot contain numbers";
  }

  // VALIDA QUE EL STRING PROPORCIONADO NO CONTENTENGA SÍIMBOLOS
  notSymbols(data) {
    if ( data.match(this.symbols) === null || data[data.match(this.symbols).index] === ' ' ) return '';
    return "Field cannot contain symbols"; 
  }

  // VALIDA QUE UN INPUT DEL TIPO TEXTO NO CONTENGA LETRAS
  onlyNums(data) {
    if (this.minus.test(data) || this.mayus.test(data) || this.symbols.test(data)) return 'El campo debe contener solo numeros';
    else return '';
  }

  // VALIDA QUE UN INPUT TENGA UN VALOR NUMERICO MAYOR A OTRO
  higherThan(data1, data2) {
    if (typeof(data1) === 'undefined') data1 = 0;
    if (typeof(data2) === 'undefined') data2 = 0;
    if ( data1 > data2 ) return '';
    return 'El campo debe contener un valor mayor al campo mínimo';
  }

  // VALIDA QUE UN INPUT TENGA UN VALOR NUMERICO MENOR A OTRO
  lesserThan(data1, data2) {
    if (typeof(data1) === 'undefined') data1 = 0;
    if (typeof(data2) === 'undefined') data2 = 0;
    if ( data1 < data2 ) return '';
    return 'El campo debe contener un valor menor al campo máximo';
  }

  // VALIDA QUE UN CAMPO SEA VERDADERO, EN CASO SEA FALSO, DEVOLVERÁ UNA DESCRIPCION INGRESADA COMO SEGUNDO PARAMETRO A LA FUNCION
  truthy(data, description) {
    console.log(data);
    console.log(description)
    if ( data === true ) return '';
    return description;
  }

  validate(state, validations) {
    let error = {}
    if ( validations.length !== 0 ) {
        validations.forEach(elem => {
            if (elem.val !== 0) {
                elem.val.forEach(val => {
                    if ( !(error.hasOwnProperty( elem.param1 ) && error[elem.param1] !== '') ) {
                        if (val === 'empty') error[elem.param1] = this.empty(state[elem.param1]);
                        if (val === 'higher') error[elem.param1] = this.higherThan(state[elem.param1], state[elem.param2]);
                        if (val === 'lesser') error[elem.param1] = this.lesserThan(state[elem.param1], state[elem.param2]);
                        if (val === 'min-length') error[elem.param1] = this.minLength(state[elem.param1], elem.param2);
                        if (val === 'not-nums') error[elem.param1] = this.notNums(state[elem.param1]);
                        if (val === 'not-symbols') error[elem.param1] = this.notSymbols(state[elem.param1]);
                        if (val === 'pass') error[elem.param1] = this.pass(state[elem.param1]);
                        if (val === 'are-equal') error[elem.param1] = this.areEqual(state[elem.param1], state[elem.param2]);
                        if (val === 'only-nums') error[elem.param1] = this.onlyNums(state[elem.param1]);
                        if (val === 'truthy') error[elem.param1] = this.truthy(state[elem.param1], elem.param2);
                    }
                });
            }
        });
    };
    console.log(error)
    return error
  }
}